import { Component, OnInit } from '@angular/core';
import { ItalianFoodsViewModel } from '../../core/models/italian-foods.component';
import { ItalianFoodsService } from '../../core/services/italian-foods/italian-foods.service';

@Component({
  selector: 'app-italian-foods',
  templateUrl: './italian-foods.component.html',
  styleUrls: ['./italian-foods.component.css']
})


export class ItalianFoodsComponent implements OnInit {

  italianFoods: ItalianFoodsViewModel[];

  constructor(private italianFoodsServices : ItalianFoodsService) 
  {
    this.italianFoods = italianFoodsServices.getAllItalianFoods();
   }

  ngOnInit() {
  }

}



