import { Injectable } from '@angular/core';
import { ItalianFoodsViewModel } from '../../models/italian-foods.component';

@Injectable()
export class ItalianFoodsService {

  constructor() { }


  getAllItalianFoods(){
    const italianFoods: ItalianFoodsViewModel[] = [
      {
      Title: 'Eat wisely Spageta!',
      Description: 'Pasta',
      Author: 'S.S.I',
      ImgSrc: './assets/images/pa.jpg'


    },
    {
      Title: 'Eat wisely some Wurst!',
      Description: 'Wurst',
      Author: 'S.S.I',
      ImgSrc: './assets/images/vurst.jpg'


    }

  
  ];
    return italianFoods;
  }
}



